:root {
  --primary-text-color: #131313;
  --title-text-color: #000;
  --title-text-color-secondary: #fcfcfc;
  --background-color: #84a178;
  --button-color: #f8d24e;
  --button-color-accent: #0a8817;
  --button-text-black: #000;
  --button-text-white: #fcfcfc;
  --transition-func: 250ms cubic-bezier(.4, 0, .2, 1);
  --dots-color: #d9d9d9;
  --dots-color-active: #84a178;
  --transition-dur-func: all 500ms cubic-bezier(.4, 0, .2, 1);
  --background-icecoffee-modal-window: #efd478;
  --background-milkshake-modal-window: #d6936d;
  --background-icecream-modal-window: #96af8c;
  --indent: 30px;
  --items: 3;
  --text-color: #fff;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--background-color);
  color: var(--primary-text-color);
  letter-spacing: .04em;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

body.no-scroll {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

button {
  cursor: url("cursor.7238bc11.png"), pointer;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  color: inherit;
  cursor: url("cursor.7238bc11.png"), pointer;
  text-decoration: none;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.button-color {
  background-color: var(--button-color);
  color: var(--button-text-black);
  transition: background-color var(--transition-func);
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
}

.button-color:hover, .button-color:focus {
  background-color: var(--button-color-accent);
}

.button-color:focus {
  color: var(--button-text-white);
}

.button-transparent {
  border: 2px solid var(--button-color);
  color: var(--button-text-black);
  transition: border-color var(--transition-func);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
}

.button-transparent:focus, .button-transparent:hover {
  border-color: var(--button-color-accent);
}

.button-transparent:focus {
  color: var(--button-text-white);
}

@media screen and (max-width: 1199px) {
  .mobile-menu {
    width: 100%;
    height: 100%;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: var(--transition-dur-func);
    background-color: #84a178;
    position: fixed;
    top: 0;
    left: 0;
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .mobile-menu__container {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 12px;
    display: flex;
    position: relative;
  }

  .mobile-menu__close-btn {
    fill: #000;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    position: absolute;
    top: 19px;
    right: 23px;
  }

  .mobile-menu__list {
    text-align: center;
    color: #fcfcfc;
    font-size: 32px;
    font-weight: 700;
    line-height: 39px;
  }

  .mobile-menu__item {
    transition: color var(--transition-func);
  }

  .mobile-menu__item:not(:last-child) {
    margin-bottom: 32px;
  }

  .mobile-menu__item:hover, .mobile-menu__item:focus {
    color: #131313;
  }

  .mobile-menu__modal-btn {
    width: 152px;
    height: 42px;
    background-color: var(--button-color);
    color: var(--button-text-black);
    transition: background-color var(--transition-func);
    border: none;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    gap: 11px;
    margin-bottom: 63px;
    margin-left: auto;
    margin-right: auto;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.21;
    display: flex;
  }

  .mobile-menu__modal-btn:hover, .mobile-menu__modal-btn:focus {
    background-color: var(--button-color-accent);
  }

  .mobile-menu__modal-btn:focus {
    color: var(--button-text-white);
  }
}

@media screen and (min-width: 768px) {
  .mobile-menu__container {
    display: block;
  }

  .mobile-menu__list {
    margin-top: 72px;
  }

  .mobile-menu__modal-btn {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .mobile-menu {
    display: none;
  }
}

.modal-button-wrapper {
  text-align: center;
}

.active-modal {
  overflow-y: hidden;
}

.backdrop {
  z-index: 9999;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

