    margin-bottom: 12px;
    padding-left: 12px;
    transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .modal-form-input:focus {
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
    outline: none;
  }

  .modal-form-comment {
